<template>
	<div>
		<!--  -->
		<el-breadcrumb separator-class="el-icon-arrow-right">
			<el-breadcrumb-item :to="{ path: '/' }"></el-breadcrumb-item>
			<el-breadcrumb-item></el-breadcrumb-item>
			<el-breadcrumb-item></el-breadcrumb-item>
		</el-breadcrumb>
		<!--  -->
		<br />
		<el-card>
			<el-row>
				<el-col :span="8">
					<el-input placeholder="">
						<el-button slot="append" icon="el-icon-search"></el-button>
					</el-input>
				</el-col>
			</el-row>
			<!--  -->
			<br />
			<el-table :data="orderlist" border stripe>
				<el-table-column type="index" label="#"></el-table-column>
				<el-table-column label="" prop="order_number"></el-table-column>
				<el-table-column label="" prop="order_price"></el-table-column>
				<el-table-column label="" prop="pay_status">
					<template v-slot="scope">
						<el-tag v-if="scope.row.pay_status == 1" type="success"
							></el-tag
						>
						<el-tag v-else type="danger"></el-tag>
					</template>
				</el-table-column>
				<el-table-column label="" prop="is_send"></el-table-column>
				<el-table-column label="" prop="create_time">
					<template v-slot="scope">
						{{ scope.row.create_time | dateFormat }}
					</template>
				</el-table-column>
				<el-table-column label="">
					<template>
						<!-- button -->
						<el-button
							type="primary"
							icon="el-icon-edit"
							size="mini"
							@click="showEditDialog"
						></el-button>
						<el-button
							type="success"
							icon="el-icon-location"
							size="mini"
							@click="showProgressBox"
						></el-button>
					</template>
				</el-table-column>
			</el-table>
			<!--  -->
			<br />
			<el-pagination
				background
				:current-page="queryInfo.pagenum"
				:page-sizes="[5, 10, 15, 20]"
				:page-size="queryInfo.pagesize"
				layout="total, sizes, prev, pager, next, jumper"
				:total="total"
				@size-change="handleSizeChange"
				@current-change="handleCurrentChange"
			></el-pagination>
		</el-card>
		<!--  -->
		<el-dialog
			title=""
			:visible.sync="editAddressDialogVisible"
			width="50%"
		>
			<el-form
				ref="editAddressForm"
				:model="editAddressForm"
				label-width="100px"
				:rules="editAddressFormRules"
			>
				<el-form-item label="" prop="address1">
					<el-cascader
						:options="cityData"
						v-model="editAddressForm.address1"
					></el-cascader>
				</el-form-item>
				<el-form-item label="" prop="address2">
					<el-input v-model="editAddressForm.address2"></el-input>
				</el-form-item>
			</el-form>
			<span slot="footer" class="dialog-footer">
				<el-button @click="addressDialogClosed"> </el-button>
				<el-button type="primary" @click="editAddress"> </el-button>
			</span>
		</el-dialog>
		<!--  -->
		<el-dialog title="" :visible.sync="progressBoxVisible" width="50%">
			<!--  -->
			<el-timeline>
				<el-timeline-item
					v-for="(item, index) in progressInfo"
					:key="index"
					:timestamp="item.time"
				>
					{{ item.context }}
				</el-timeline-item>
			</el-timeline>
		</el-dialog>
	</div>
</template>

<script>
import cityData from "./citydata";
export default {
	data() {
		return {
			queryInfo: {
				query   : "",
				pagenum : 1,
				pagesize: 10
			},
			total                   : 0,
			orderlist               : [],
			editAddressDialogVisible: false,
			editAddressForm         : {
				address1: [],
				address2: ""
			},
			editAddressFormRules: {
				address1: [{ required: true, message: "", trigger: "blur" }],
				address2: [{ required: true, message: "", trigger: "blur" }]
			},
			cityData,
			progressBoxVisible: false,
			progressInfo      : []
		};
	},
	methods: {
		async getOrderList() {
			const { data: res } = await this.$http.get("orders", {params: this.queryInfo});
			//
			if (res.meta.status !== 200) {
				return this.$message.error("");
			}
			//
			this.orderlist = res.data.goods;
			this.total = res.data.total;
		},
		//
		handleSizeChange(newSize) {
			this.queryInfo.pagesize = newSize;
			this.getOrderList();
		},
		//
		handleCurrentChange(newPage) {
			this.queryInfo.pagenum = newPage;
			this.getOrderList();
		},
		//
		showEditDialog() {
			this.editAddressDialogVisible = true;
		},
		//
		addressDialogClosed() {
			this.$refs.editAddressForm.resetFields();
			this.editAddressDialogVisible = false;
		},
		//
		editAddress() {
			this.$refs.editAddressForm.validate(async(valid) => {
				if (!valid) {
					return false;
				}
				const { data: res } = await this.$http.put(
					"orders/:id",
					this.editAddressForm
				);
				if (res.meta.status !== 200) {
					return this.$message.error("");
				}
				this.$message.success("");
				this.editAddressDialogVisible = false;
				await this.getOrderList();
			});
		},
		//
		async showProgressBox() {
			this.progressBoxVisible = true;
			const { data: res } = await this.$http.get("kuaidi/1106975712662");
			//
			if (res.meta.status !== 200) {
				return this.$message.error("");
			}
			//
			this.progressBoxVisible = true;
			this.progressInfo = res.data;
			console.log(this.progressInfo);
		}
	},
	mounted() {
		this.getOrderList();
	}
};
</script>

<style lang="scss" scoped>
::v-deep .el-table .el-table__cell {
	text-align: center;
}
.el-cascader {
	width: 100%;
}
</style>
